import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import {
  Meeting,
  MeetingResponse,
  MeetingDetailResponse,
  MeetingCreateRequest,
  MeetingUpdateRequest,
  MeetingStatusUpdateRequest,
  MeetingStatus,
  UserNamesResponse,
  MeetingRecommendationRequest,
  MeetingRecommendationResponse,
  MeetingAttachmentRequest,
  MeetingAttachmentResponse,
  MeetingMinutesRequest,
  MeetingMinutesResponse,
} from '../models/meeting.model';

@Injectable({
  providedIn: 'root',
})
export class MeetingsService {
  private baseUrl = environment.apiBaseUrl;

  constructor(private http: HttpClient) {}

  getMeetings(
    search?: string,
    status?: MeetingStatus,
    from?: string,
    to?: string,
    pageNumber: number = 1,
    pageSize: number = 10
  ): Observable<MeetingResponse> {
    let params = new HttpParams()
      .set('PageNumber', pageNumber.toString())
      .set('PageSize', pageSize.toString());

    if (search) {
      params = params.set('Search', search);
    }
    if (status !== undefined) {
      params = params.set('Status', status.toString());
    }
    if (from) {
      params = params.set('From', from);
    }
    if (to) {
      params = params.set('To', to);
    }

    return this.http.get<MeetingResponse>(`${this.baseUrl}/Meetings`, {
      params,
    });
  }

  getMeetingById(id: number): Observable<MeetingDetailResponse> {
    return this.http.get<MeetingDetailResponse>(
      `${this.baseUrl}/Meetings/${id}`
    );
  }

  createMeeting(
    meeting: MeetingCreateRequest
  ): Observable<MeetingDetailResponse> {
    console.log('Service - Meeting data:', meeting);

    return this.http.post<MeetingDetailResponse>(
      `${this.baseUrl}/Meetings`,
      meeting
    );
  }

  updateMeeting(
    id: number,
    meeting: MeetingUpdateRequest
  ): Observable<MeetingDetailResponse> {
    return this.http.put<MeetingDetailResponse>(
      `${this.baseUrl}/Meetings/${id}`,
      meeting
    );
  }

  deleteMeeting(id: number): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    return this.http.delete<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/Meetings/${id}`);
  }

  updateMeetingStatus(
    id: number,
    statusUpdate: { status: MeetingStatus }
  ): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    const requestBody = {
      status: statusUpdate.status,
    };

    console.log('Update Meeting Status Request Body:', requestBody);
    console.log('Status value type:', typeof statusUpdate.status);

    return this.http.patch<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/Meetings/${id}/status`, requestBody);
  }

  // Get user names for attendees
  getUserNames(search?: string): Observable<UserNamesResponse> {
    let url = `${this.baseUrl}/Users/names`;
    if (search) {
      url += `?search=${encodeURIComponent(search)}`;
    }
    return this.http.get<UserNamesResponse>(url);
  }

  // Get all users for assignment dropdown
  getAllUsers(): Observable<UserNamesResponse> {
    return this.http.get<UserNamesResponse>(`${this.baseUrl}/Users/names`);
  }

  // Add attendee to meeting
  addAttendee(meetingId: number, attendee: any): Observable<any> {
    return this.http.post(
      `${this.baseUrl}/Meetings/${meetingId}/attendees`,
      attendee
    );
  }

  // Update attendee
  updateAttendee(
    meetingId: number,
    attendeeId: number,
    attendee: any
  ): Observable<any> {
    return this.http.put(
      `${this.baseUrl}/Meetings/${meetingId}/attendees/${attendeeId}`,
      attendee
    );
  }

  // Delete attendee
  deleteAttendee(meetingId: number, attendeeId: number): Observable<any> {
    return this.http.delete(
      `${this.baseUrl}/Meetings/${meetingId}/attendees/${attendeeId}`
    );
  }

  // Add recommendation to meeting
  addRecommendation(
    meetingId: number,
    recommendation: MeetingRecommendationRequest
  ): Observable<MeetingRecommendationResponse> {
    return this.http.post<MeetingRecommendationResponse>(
      `${this.baseUrl}/Meetings/${meetingId}/recommendations`,
      recommendation
    );
  }

  // Update recommendation
  updateRecommendation(
    recommendationId: number,
    recommendation: MeetingRecommendationRequest
  ): Observable<MeetingRecommendationResponse> {
    return this.http.put<MeetingRecommendationResponse>(
      `${this.baseUrl}/Meetings/recommendations/${recommendationId}`,
      recommendation
    );
  }

  // Delete recommendation
  deleteRecommendation(recommendationId: number): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    return this.http.delete<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/Meetings/recommendations/${recommendationId}`);
  }

  // Add attachment to meeting
  addAttachment(
    meetingId: number,
    attachment: MeetingAttachmentRequest
  ): Observable<MeetingAttachmentResponse> {
    const formData = new FormData();
    formData.append('File', attachment.file);
    formData.append('Description', attachment.description);
    formData.append('DocumentSource', attachment.documentSource.toString());

    console.log('Service - Attachment data:', attachment);
    console.log('Service - FormData entries:');
    for (let [key, value] of formData.entries()) {
      console.log(`${key}:`, value);
    }
    console.log('Service - Meeting ID:', meetingId);
    console.log(
      'Service - DocumentSource type:',
      typeof attachment.documentSource
    );
    console.log('Service - DocumentSource value:', attachment.documentSource);

    return this.http.post<MeetingAttachmentResponse>(
      `${this.baseUrl}/Meetings/${meetingId}/attachments`,
      formData
    );
  }

  // Delete attachment
  deleteAttachment(
    meetingId: number,
    attachmentId: number
  ): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    return this.http.delete<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/Meetings/${meetingId}/attachments/${attachmentId}`);
  }

  // Add minutes to meeting
  addMinutes(
    meetingId: number,
    minutes: MeetingMinutesRequest
  ): Observable<MeetingMinutesResponse> {
    return this.http.post<MeetingMinutesResponse>(
      `${this.baseUrl}/Meetings/${meetingId}/minutes`,
      minutes
    );
  }

  // Update minutes
  updateMinutes(
    meetingId: number,
    minutes: MeetingMinutesRequest
  ): Observable<MeetingMinutesResponse> {
    return this.http.put<MeetingMinutesResponse>(
      `${this.baseUrl}/Meetings/${meetingId}/minutes`,
      minutes
    );
  }

  // Update attendee status
  updateAttendeeStatus(
    meetingId: number,
    attendeeId: number,
    statusUpdate: { attendanceStatus: number; notes?: string }
  ): Observable<any> {
    return this.http.patch(
      `${this.baseUrl}/Meetings/${meetingId}/attendees/${attendeeId}/status`,
      statusUpdate
    );
  }

  // Download attachment
  downloadAttachment(
    meetingId: number,
    attachmentId: number
  ): Observable<Blob> {
    return this.http.get(
      `${this.baseUrl}/Meetings/${meetingId}/attachments/${attachmentId}/download`,
      { responseType: 'blob' }
    );
  }
}
