export interface Meeting {
  id: number;
  title: string;
  agenda: string;
  type: MeetingType;
  locationMode: MeetingLocationMode;
  organizerUserId: string;
  organizerName: string | null;
  startAt: string;
  endAt: string;
  status: MeetingStatus;
  locationText: string | null;
  onlineUrl: string | null;
  createdByUserId: string;
  createdAt: string;
  updatedAt: string | null;
  attendees: MeetingAttendee[];
  attachments: MeetingAttachment[];
  minutes: MeetingMinutes | null;
  recommendations: MeetingRecommendation[];
}

export interface MeetingAttendee {
  id: number;
  meetingId: number;
  kind: AttendeeKind;
  userId: string | null;
  userName: string | null;
  displayName: string | null;
  organization: string | null;
  jobTitle: string | null;
  department: string | null;
  email: string | null;
  role: AttendeeRole;
  attendanceStatus: AttendanceStatus;
  notes: string | null;
}

export interface MeetingAttachment {
  id: number;
  meetingId: number;
  filePath: string;
  fileType: string;
  fileName: string;
  uploadedByUserId: string;
  uploadedByName: string;
  description: string;
  uploadedAt: string;
  documentSource: DocumentSource;
}

export interface MeetingResponse {
  success: boolean;
  message: string;
  data: {
    totalCount: number;
    page: number;
    pageSize: number;
    items: Meeting[];
  };
  errors: string[];
}

export interface MeetingDetailResponse {
  success: boolean;
  message: string;
  data: Meeting;
  errors: string[];
}

export interface MeetingCreateRequest {
  Title: string;
  Agenda: string;
  Type: MeetingType;
  LocationMode: MeetingLocationMode;
  StartAt: string;
  EndAt: string;
  LocationText: string;
  OnlineUrl?: string;
  Attendees: MeetingAttendeeRequest[];
}

export interface MeetingUpdateRequest {
  title: string;
  agenda: string;
  type: MeetingType;
  locationMode: MeetingLocationMode;
  startAt: string;
  endAt: string;
  locationText: string;
  onlineUrl?: string;
}

export interface MeetingAttendeeRequest {
  Kind: AttendeeKind;
  UserId?: string; // if Internal
  DisplayName?: string; // if External
  Organization?: string; // if External
  JobTitle?: string; // if External
  Department?: string; // if External
  Email?: string; // if External
  Role: AttendeeRole;
  Notes?: string;
}

export interface UserName {
  id: string;
  name: string;
  departmentName: string;
}

export interface UserNamesResponse {
  success: boolean;
  message: string;
  data: UserName[];
  errors: string[];
}

export interface MeetingRecommendationRequest {
  text: string;
  assigneeUserId?: string;
  dueDate?: string;
}

export interface MeetingRecommendationResponse {
  success: boolean;
  message: string;
  data: {
    id: number;
    meetingId: number;
    text: string;
  };
  errors: string[];
}

export interface MeetingAttachmentRequest {
  file: File;
  description: string;
  documentSource: DocumentSource;
}

export interface MeetingAttachmentResponse {
  success: boolean;
  message: string;
  data: {
    id: number;
    meetingId: number;
    filePath: string;
    fileType: string;
    fileName: string;
    uploadedByUserId: string;
    uploadedByName: string;
    description: string;
    uploadedAt: string;
    documentSource: DocumentSource;
  };
  errors: string[];
}

export interface MeetingMinutes {
  id: number;
  meetingId: number;
  notes: string;
  attachmentsCount: number;
  summaryAI: string;
}

export interface MeetingMinutesRequest {
  notes?: string;
  summaryAI?: string;
}

export interface MeetingMinutesResponse {
  success: boolean;
  message: string;
  data: {
    id: number;
    meetingId: number;
    notes: string;
    attachmentsCount: number;
    summaryAI: string;
  };
  errors: string[];
}

export interface MeetingRecommendation {
  id: number;
  meetingId: number;
  text: string;
  assigneeUserId: string | null;
  fullName: string | null;
  dueDate: string | null;
  createdAt: string;
}

export enum DocumentSource {
  Incoming = 0,
  Outgoing = 1,
  Internal = 2,
}

export interface MeetingStatusUpdateRequest {
  statusDto: {
    status: MeetingStatus;
  };
}

export enum MeetingStatus {
  Scheduled = 0,
  InProgress = 1,
  Done = 2,
  Cancelled = 3,
}

export enum MeetingType {
  Internal = 0,
  External = 1,
  Other = 2,
}

export enum MeetingLocationMode {
  InPerson = 0,
  Online = 1,
  Hybrid = 2,
}

export enum AttendeeKind {
  Internal = 0,
  External = 1,
}

export enum RSVP {
  Invited = 0,
  Accepted = 1,
  Declined = 2,
  Attended = 3,
  Absent = 4,
}

export enum PlaceType {
  InternalRoom = 0,
  ExternalVenue = 1,
  Other = 2,
}

export enum AttendeeRole {
  Required = 0,
  Optional = 1,
  Speaker = 2,
}

export enum AttendanceStatus {
  Invited = 0,
  Accepted = 1,
  Declined = 2,
  Attended = 3,
  Absent = 4,
}
