import {
  Component,
  EventEmitter,
  Input,
  Output,
  OnChanges,
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MeetingsService } from '../../../services/meetings.service';
import { TranslationService } from '../../../services/translation.service';
import { LangService } from '../../../services/lang.service';
import { Meeting, MeetingStatus } from '../../../models/meeting.model';

@Component({
  selector: 'app-update-status-modal',
  templateUrl: './update-status-modal.component.html',
  styleUrls: ['./update-status-modal.component.css'],
  standalone: false,
})
export class UpdateStatusModalComponent implements OnChanges {
  @Input() meeting: Meeting | null = null;
  @Input() isVisible = false;
  @Output() statusUpdated = new EventEmitter<Meeting>();
  @Output() modalClosed = new EventEmitter<void>();

  statusForm: FormGroup;
  loading = false;
  currentLang = 'en';
  isRTL = false;

  meetingStatuses = [
    { value: MeetingStatus.Scheduled, label: 'meetings.status.scheduled' },
    { value: MeetingStatus.InProgress, label: 'meetings.status.in_progress' },
    { value: MeetingStatus.Done, label: 'meetings.status.done' },
    { value: MeetingStatus.Cancelled, label: 'meetings.status.cancelled' },
  ];

  constructor(
    private fb: FormBuilder,
    private meetingsService: MeetingsService,
    private translationService: TranslationService,
    private langService: LangService
  ) {
    this.statusForm = this.fb.group({
      status: ['', Validators.required],
    });

    this.langService.currentLang$.subscribe((lang) => {
      this.currentLang = lang;
      this.isRTL = lang === 'ar';
    });
  }

  ngOnChanges(): void {
    if (this.meeting && this.isVisible) {
      // Only patch value if modal is visible and meeting is set
      this.statusForm.patchValue({
        status: this.meeting.status,
      });
    }
  }

  onSubmit(): void {
    if (this.statusForm.valid && this.meeting) {
      this.loading = true;
      const newStatus = Number(this.statusForm.value.status); // Convert to number

      this.meetingsService
        .updateMeetingStatus(this.meeting.id, { status: newStatus })
        .subscribe({
          next: (response) => {
            if (response.success) {
              // Update the meeting status locally
              const updatedMeeting = { ...this.meeting!, status: newStatus };
              this.statusUpdated.emit(updatedMeeting);
              this.closeModal();
            }
            this.loading = false;
          },
          error: (error) => {
            console.error('Error updating meeting status:', error);
            this.loading = false;
          },
        });
    }
  }

  closeModal(): void {
    this.modalClosed.emit();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  getFieldError(fieldName: string): string {
    const field = this.statusForm.get(fieldName);
    if (field?.invalid && field?.touched) {
      if (field.errors?.['required']) {
        return this.translate('common.field_required');
      }
    }
    return '';
  }
}
