import { Component, OnInit, signal } from '@angular/core';
import { MeetingsService } from '../../../services/meetings.service';
import { TranslationService } from '../../../services/translation.service';
import { LangService } from '../../../services/lang.service';
import { Meeting, MeetingStatus } from '../../../models/meeting.model';
import {
  ConfirmationModalComponent,
  ConfirmationModalData,
} from '../../shared/confirmation-modal/confirmation-modal.component';

@Component({
  selector: 'app-meetings-list',
  templateUrl: './meetings-list.component.html',
  styleUrls: ['./meetings-list.component.css'],
  standalone: false,
})
export class MeetingsListComponent implements OnInit {
  meetings = signal<Meeting[]>([]);
  loading = signal(false);
  viewMode = signal<'cards' | 'table'>('cards');
  currentLang = signal<'ar' | 'en'>('en');
  isRTL = signal(false);

  // Math property for template
  Math = Math;

  // Pagination state
  currentPage = signal(1);
  pageSize = signal(10);
  totalCount = signal(0);
  totalPages = signal(0);

  // Search and filter state
  searchTerm = signal('');
  statusFilter = signal<MeetingStatus | undefined>(undefined);
  fromDate = signal('');
  toDate = signal('');

  // Modal state
  showDeleteModal = signal(false);
  meetingToDelete = signal<Meeting | null>(null);
  deleteModalData = signal<ConfirmationModalData>({
    title: '',
    message: '',
    confirmText: '',
    cancelText: '',
    type: 'delete',
  });

  // Edit/Status modal state
  showEditModal = signal(false);
  showStatusModal = signal(false);
  meetingToEdit = signal<Meeting | null>(null);
  meetingToUpdateStatus = signal<Meeting | null>(null);

  // Success modal state
  showSuccessModal = signal(false);
  successMessage = signal('');

  constructor(
    private meetingsService: MeetingsService,
    private translationService: TranslationService,
    private langService: LangService
  ) {}

  ngOnInit(): void {
    this.loadMeetings();
    this.langService.currentLang$.subscribe((lang) => {
      this.currentLang.set(lang);
      this.isRTL.set(lang === 'ar');
    });
  }

  loadMeetings(): void {
    this.loading.set(true);

    // Convert date strings to ISO format for API
    let fromDateISO = undefined;
    let toDateISO = undefined;

    if (this.fromDate()) {
      fromDateISO = new Date(this.fromDate() + 'T00:00:00').toISOString();
    }

    if (this.toDate()) {
      toDateISO = new Date(this.toDate() + 'T23:59:59').toISOString();
    }

    this.meetingsService
      .getMeetings(
        this.searchTerm() || undefined,
        this.statusFilter(),
        fromDateISO,
        toDateISO,
        this.currentPage(),
        this.pageSize()
      )
      .subscribe({
        next: (response) => {
          if (response && response.success) {
            if (response.data && response.data.items) {
              this.meetings.set(response.data.items);
              this.totalCount.set(response.data.totalCount || 0);
              this.totalPages.set(
                Math.ceil((response.data.totalCount || 0) / this.pageSize())
              );
            }
          }
          this.loading.set(false);
        },
        error: (error) => {
          console.error('Error loading meetings:', error);
          this.loading.set(false);
        },
      });
  }

  openDeleteModal(meeting: Meeting): void {
    this.meetingToDelete.set(meeting);
    this.deleteModalData.set({
      title: this.translate('meetings.delete_meeting'),
      message: this.translate('meetings.confirm_delete_meeting').replace(
        '%title%',
        meeting.title
      ),
      confirmText: this.translate('meetings.delete'),
      cancelText: this.translate('meetings.cancel'),
      type: 'delete',
    });
    this.showDeleteModal.set(true);
  }

  onDeleteConfirm(): void {
    const meeting = this.meetingToDelete();
    if (meeting) {
      this.meetingsService.deleteMeeting(meeting.id).subscribe({
        next: (response) => {
          if (response.success) {
            this.meetings.update((meetings) =>
              meetings.filter((m) => m.id !== meeting.id)
            );
            this.showDeleteModal.set(false);
            this.meetingToDelete.set(null);
            this.successMessage.set(this.translate('meetings.deleted_success'));
            this.showSuccessModal.set(true);
            setTimeout(() => {
              this.showSuccessModal.set(false);
            }, 3000);
          }
        },
        error: (error) => {
          console.error('Error deleting meeting:', error);
          this.showDeleteModal.set(false);
          this.meetingToDelete.set(null);
        },
      });
    }
  }

  onDeleteCancel(): void {
    this.showDeleteModal.set(false);
    this.meetingToDelete.set(null);
  }

  openEditModal(meeting: Meeting): void {
    this.meetingToEdit.set(meeting);
    this.showEditModal.set(true);
  }

  openStatusModal(meeting: Meeting): void {
    this.meetingToUpdateStatus.set(meeting);
    this.showStatusModal.set(true);
  }

  onEditModalClose(): void {
    this.showEditModal.set(false);
    this.meetingToEdit.set(null);
  }

  onStatusModalClose(): void {
    this.showStatusModal.set(false);
    this.meetingToUpdateStatus.set(null);
  }

  onMeetingUpdated(updatedMeeting: Meeting): void {
    this.meetings.update((meetings) =>
      meetings.map((m) => (m.id === updatedMeeting.id ? updatedMeeting : m))
    );
    this.showEditModal.set(false);
    this.meetingToEdit.set(null);
    this.successMessage.set(this.translate('meetings.updated_success'));
    this.showSuccessModal.set(true);
    setTimeout(() => {
      this.showSuccessModal.set(false);
    }, 3000);
  }

  onStatusUpdated(updatedMeeting: Meeting): void {
    this.meetings.update((meetings) =>
      meetings.map((m) => (m.id === updatedMeeting.id ? updatedMeeting : m))
    );
    this.showStatusModal.set(false);
    this.meetingToUpdateStatus.set(null);
    this.successMessage.set(this.translate('meetings.status_updated_success'));
    this.showSuccessModal.set(true);
    setTimeout(() => {
      this.showSuccessModal.set(false);
    }, 3000);
  }

  closeSuccessModal(): void {
    this.showSuccessModal.set(false);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  formatDate(dateString: string): string {
    return new Date(dateString).toLocaleDateString();
  }

  getMeetingStatusText(status: MeetingStatus): string {
    switch (status) {
      case MeetingStatus.Scheduled:
        return this.translate('meetings.status.scheduled');
      case MeetingStatus.InProgress:
        return this.translate('meetings.status.in_progress');
      case MeetingStatus.Done:
        return this.translate('meetings.status.done');
      case MeetingStatus.Cancelled:
        return this.translate('meetings.status.cancelled');
      default:
        return this.translate('meetings.status.unknown');
    }
  }

  getMeetingStatusClass(status: MeetingStatus): string {
    switch (status) {
      case MeetingStatus.Scheduled:
        return 'status-scheduled';
      case MeetingStatus.InProgress:
        return 'status-in-progress';
      case MeetingStatus.Done:
        return 'status-done';
      case MeetingStatus.Cancelled:
        return 'status-cancelled';
      default:
        return 'status-unknown';
    }
  }

  getMeetingTypeText(type: number): string {
    switch (type) {
      case 0:
        return this.translate('meetings.type.internal');
      case 1:
        return this.translate('meetings.type.external');
      case 2:
        return this.translate('meetings.type.other');
      default:
        return this.translate('meetings.type.unknown');
    }
  }

  getLocationModeText(mode: number): string {
    switch (mode) {
      case 0:
        return this.translate('meetings.location.in_person');
      case 1:
        return this.translate('meetings.location.online');
      case 2:
        return this.translate('meetings.location.hybrid');
      default:
        return this.translate('meetings.location.unknown');
    }
  }

  // Pagination methods
  onPageChange(page: number): void {
    this.currentPage.set(page);
    this.loadMeetings();
  }

  onPageSizeChange(event: Event): void {
    const size = +(event.target as HTMLSelectElement).value;
    this.pageSize.set(size);
    this.currentPage.set(1);
    this.loadMeetings();
  }

  onSearch(event: Event): void {
    const search = (event.target as HTMLInputElement).value;
    this.searchTerm.set(search);
    this.currentPage.set(1);
    this.loadMeetings();
  }

  onStatusFilterChange(event: Event): void {
    const value = (event.target as HTMLSelectElement).value;
    this.statusFilter.set(value === '' ? undefined : +value);
    this.currentPage.set(1);
    this.loadMeetings();
  }

  /**
   * Handle date filter changes
   * This method is called when user changes either fromDate or toDate
   * It resets to page 1 and reloads meetings with new date filters
   */
  onDateFilter(): void {
    this.currentPage.set(1);
    this.loadMeetings();
  }

  clearFilters(): void {
    this.searchTerm.set('');
    this.statusFilter.set(undefined);
    this.fromDate.set('');
    this.toDate.set('');
    this.currentPage.set(1);
    this.loadMeetings();
  }

  getPageNumbers(): number[] {
    const pages: number[] = [];
    const totalPages = this.totalPages();
    const currentPage = this.currentPage();

    const start = Math.max(1, currentPage - 2);
    const end = Math.min(totalPages, currentPage + 2);

    for (let i = start; i <= end; i++) {
      pages.push(i);
    }

    return pages;
  }
}
