import {
  Component,
  OnInit,
  signal,
  computed,
  inject,
  ChangeDetectorRef,
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  AbstractControl,
} from '@angular/forms';
import { Router } from '@angular/router';
import {
  Observable,
  of,
  debounceTime,
  distinctUntilChanged,
  switchMap,
  catchError,
} from 'rxjs';
import { TranslationService } from '../../../services/translation.service';
import { MeetingsService } from '../../../services/meetings.service';
import { LangService } from '../../../services/lang.service';
import {
  MeetingCreateRequest,
  MeetingAttendeeRequest,
  MeetingType,
  MeetingLocationMode,
  AttendeeKind,
  AttendeeRole,
  UserName,
  UserNamesResponse,
  MeetingRecommendationRequest,
  MeetingDetailResponse,
} from '../../../models/meeting.model';

@Component({
  selector: 'app-add-meeting',
  templateUrl: './add-meeting.component.html',
  styleUrls: ['./add-meeting.component.css'],
  standalone: false,
})
export class AddMeetingComponent implements OnInit {
  private fb = inject(FormBuilder);
  private router = inject(Router);
  private meetingsService = inject(MeetingsService);
  private translationService = inject(TranslationService);
  private langService = inject(LangService);
  private cdr = inject(ChangeDetectorRef);

  // Signals
  isRTL = signal(false);
  isLoading = signal(false);
  isSubmitting = signal(false);
  isAddingRecommendation = signal(false);
  error = signal('');
  success = signal('');
  meetingCreated = signal(false);
  createdMeetingId = signal<number | null>(null);
  filteredUsers = signal<UserName[]>([]);
  recommendations = signal<{text: string, assigneeUserId?: string}[]>([]);
  allUsers: any[] = [];
  currentLocationMode = signal<MeetingLocationMode>(
    MeetingLocationMode.InPerson
  );

  // Form
  meetingForm!: FormGroup;

  // Enums for template
  MeetingType = MeetingType;
  MeetingLocationMode = MeetingLocationMode;
  AttendeeKind = AttendeeKind;
  AttendeeRole = AttendeeRole;

  // Computed values
  showLocationText = computed(() => {
    const mode = this.currentLocationMode();
    return (
      mode === MeetingLocationMode.InPerson ||
      mode === MeetingLocationMode.Hybrid
    );
  });
  showOnlineUrl = computed(() => {
    const mode = this.currentLocationMode();
    return (
      mode === MeetingLocationMode.Online || mode === MeetingLocationMode.Hybrid
    );
  });
  // Signals for attendee kind tracking
  currentAttendeeKinds = signal<AttendeeKind[]>([]);

  // Computed values for attendee display
  showInternalFields = computed(() => {
    return this.currentAttendeeKinds().map(
      (kind) => kind === AttendeeKind.Internal
    );
  });

  showExternalFields = computed(() => {
    return this.currentAttendeeKinds().map(
      (kind) => kind === AttendeeKind.External
    );
  });

  ngOnInit(): void {
    this.isRTL.set(this.langService.isRTL());
    this.initializeForm();
    this.loadAllUsers();
    // Add a default attendee
    this.addAttendee();

    // Set initial location mode
    this.currentLocationMode.set(MeetingLocationMode.InPerson);

    // Initialize attendee kinds array
    this.currentAttendeeKinds.set([AttendeeKind.Internal]);
  }

  private initializeForm(): void {
    this.meetingForm = this.fb.group({
      title: ['', [Validators.required, Validators.minLength(3)]],
      agenda: ['', [Validators.required, Validators.minLength(3)]],
      type: [MeetingType.Internal, Validators.required],
      locationMode: [MeetingLocationMode.InPerson, Validators.required],
      startAt: ['', Validators.required],
      endAt: ['', Validators.required],
      locationText: [''],
      onlineUrl: [''],
      attendees: this.fb.array(
        [],
        [Validators.required, Validators.minLength(1)]
      ),
    });

    // Set default values for testing
    setTimeout(() => {
      this.meetingForm.patchValue({
        title: '',
        agenda: '',
        type: MeetingType.Internal,
        locationMode: MeetingLocationMode.InPerson,
        startAt: '',
        endAt: '',
        locationText: '',
      });
    }, 100);

    // Add conditional validators based on location mode
    this.meetingForm.get('locationMode')?.valueChanges.subscribe((mode) => {
      // Convert to number if it's a string
      const numericMode = typeof mode === 'string' ? parseInt(mode, 10) : mode;
      this.updateLocationValidators(numericMode);
    });

    // Add time validation
    this.meetingForm.get('startAt')?.valueChanges.subscribe(() => {
      this.validateTimeRange();
    });
    this.meetingForm.get('endAt')?.valueChanges.subscribe(() => {
      this.validateTimeRange();
    });
  }

  private updateLocationValidators(mode: MeetingLocationMode): void {
    // Update the signal first
    this.currentLocationMode.set(mode);

    const locationTextControl = this.meetingForm.get('locationText');
    const onlineUrlControl = this.meetingForm.get('onlineUrl');

    // Clear existing validators
    locationTextControl?.clearValidators();
    onlineUrlControl?.clearValidators();

    // Add validators based on mode
    switch (mode) {
      case MeetingLocationMode.InPerson:
        locationTextControl?.setValidators([Validators.required]);
        onlineUrlControl?.setValue('');
        break;
      case MeetingLocationMode.Online:
        onlineUrlControl?.setValidators([Validators.required]);
        locationTextControl?.setValue('');
        break;
      case MeetingLocationMode.Hybrid:
        locationTextControl?.setValidators([Validators.required]);
        onlineUrlControl?.setValidators([Validators.required]);
        break;
    }

    locationTextControl?.updateValueAndValidity();
    onlineUrlControl?.updateValueAndValidity();
  }

  private validateTimeRange(): void {
    const startAt = this.meetingForm.get('startAt')?.value;
    const endAt = this.meetingForm.get('endAt')?.value;

    if (startAt && endAt) {
      const startDate = new Date(startAt);
      const endDate = new Date(endAt);

      if (endDate <= startDate) {
        this.meetingForm.get('endAt')?.setErrors({ invalidTimeRange: true });
      } else {
        this.meetingForm.get('endAt')?.setErrors(null);
      }
    }
  }

  // Form getters
  get attendees(): FormArray {
    return this.meetingForm.get('attendees') as FormArray;
  }

  private loadAllUsers(): void {
    this.getAllUsers().subscribe((users) => {
      this.filteredUsers.set(users);
      this.allUsers = users;
    });
  }

  // Attendee management
  addAttendee(): void {
    const attendeeGroup = this.fb.group({
      kind: [AttendeeKind.Internal, Validators.required],
      userId: [''],
      displayName: [''],
      organization: [''],
      jobTitle: [''],
      department: [''],
      email: ['', [Validators.email]],
      role: [AttendeeRole.Required, Validators.required],
      notes: [''],
    });

    // Add to form array first
    this.attendees.push(attendeeGroup);

    // Then setup validators with correct index
    const index = this.attendees.length - 1;
    this.setupAttendeeValidators(attendeeGroup, index);

    // Update attendee kinds array
    const currentKinds = this.currentAttendeeKinds();
    currentKinds.push(AttendeeKind.Internal);
    this.currentAttendeeKinds.set([...currentKinds]);
  }

  removeAttendee(index: number): void {
    this.attendees.removeAt(index);

    // Update the attendee kinds array
    const currentKinds = this.currentAttendeeKinds();
    currentKinds.splice(index, 1);
    this.currentAttendeeKinds.set([...currentKinds]);
  }

  private updateAttendeeKind(index: number, kind: AttendeeKind): void {
    const currentKinds = this.currentAttendeeKinds();
    currentKinds[index] = kind;
    this.currentAttendeeKinds.set([...currentKinds]);
  }

  private setupAttendeeValidators(
    attendeeGroup: FormGroup,
    index: number
  ): void {
    const kindControl = attendeeGroup.get('kind');
    const userIdControl = attendeeGroup.get('userId');
    const displayNameControl = attendeeGroup.get('displayName');
    const organizationControl = attendeeGroup.get('organization');
    const jobTitleControl = attendeeGroup.get('jobTitle');
    const departmentControl = attendeeGroup.get('department');
    const emailControl = attendeeGroup.get('email');

    // Set initial validators based on current value
    const initialKind = kindControl?.value;
    this.updateAttendeeKind(index, initialKind);
    this.setAttendeeValidators(
      initialKind,
      userIdControl,
      displayNameControl,
      organizationControl,
      jobTitleControl,
      departmentControl,
      emailControl
    );

    kindControl?.valueChanges.subscribe((kind) => {
      // Update the signal with the correct index
      this.updateAttendeeKind(index, kind);
      this.setAttendeeValidators(
        kind,
        userIdControl,
        displayNameControl,
        organizationControl,
        jobTitleControl,
        departmentControl,
        emailControl
      );
    });
  }

  private setAttendeeValidators(
    kind: AttendeeKind,
    userIdControl: AbstractControl | null,
    displayNameControl: AbstractControl | null,
    organizationControl: AbstractControl | null,
    jobTitleControl: AbstractControl | null,
    departmentControl: AbstractControl | null,
    emailControl: AbstractControl | null
  ): void {
    if (kind === AttendeeKind.Internal) {
      userIdControl?.setValidators([Validators.required]);
      displayNameControl?.clearValidators();
      organizationControl?.clearValidators();
      jobTitleControl?.clearValidators();
      departmentControl?.clearValidators();
      emailControl?.clearValidators();
    } else {
      userIdControl?.clearValidators();
      displayNameControl?.setValidators([Validators.required]);
      organizationControl?.setValidators([Validators.required]);
      jobTitleControl?.setValidators([Validators.required]);
      departmentControl?.setValidators([Validators.required]);
      emailControl?.setValidators([Validators.required, Validators.email]);
    }

    userIdControl?.updateValueAndValidity();
    displayNameControl?.updateValueAndValidity();
    organizationControl?.updateValueAndValidity();
    jobTitleControl?.updateValueAndValidity();
    departmentControl?.updateValueAndValidity();
    emailControl?.updateValueAndValidity();
  }

  // Get all users for internal attendees dropdown
  getAllUsers(): Observable<UserName[]> {
    return this.meetingsService.getUserNames('').pipe(
      catchError(() =>
        of({
          success: false,
          data: [],
          message: '',
          errors: [],
        } as UserNamesResponse)
      ),
      switchMap((response) => of(response.success ? response.data : []))
    );
  }

  // Get user name by ID
  getUserNameById(userId: string): string {
    const user = this.allUsers.find(u => u.id === userId);
    return user ? `${user.name} - ${user.departmentName}` : 'Unknown User';
  }

  onAttendeeKindChange(event: any, index: number): void {
    const target = event?.target as HTMLSelectElement;
    if (!target) return;

    const kind = parseInt(target.value, 10) as AttendeeKind;
    this.updateAttendeeKind(index, kind);
  }

  onUserSelect(event: any, attendeeIndex: number): void {
    const target = event?.target as HTMLSelectElement;
    if (!target) return;

    const userId = target.value;
    if (!userId) return;

    const user = this.filteredUsers().find((u) => u.id === userId);
    if (user) {
      const attendeeGroup = this.attendees.at(attendeeIndex) as FormGroup;
      attendeeGroup.patchValue({
        userId: user.id,
        displayName: user.name,
        department: user.departmentName,
      });
    }
  }

  // Form submission
  onSubmit(): void {
    if (this.meetingForm.valid && !this.isSubmitting()) {
      this.isSubmitting.set(true);
      this.error.set('');

      const formValue = this.meetingForm.value;


      // Only send basic meeting data, not recommendations/attachments
      const meetingData: MeetingCreateRequest = {
        Title: formValue.title,
        Agenda: formValue.agenda,
        Type: parseInt(formValue.type, 10) as MeetingType,
        LocationMode: parseInt(
          formValue.locationMode,
          10
        ) as MeetingLocationMode,
        StartAt: new Date(formValue.startAt).toISOString(),
        EndAt: new Date(formValue.endAt).toISOString(),
        LocationText: formValue.locationText || '',
        OnlineUrl: formValue.onlineUrl || '',
        Attendees: this.buildAttendeesArray(formValue.attendees),
      };

      this.meetingsService.createMeeting(meetingData).subscribe({
        next: (response: MeetingDetailResponse) => {
          if (response.success) {
            this.success.set(response.message);
            this.createdMeetingId.set(response.data.id);
            this.meetingCreated.set(true);
            this.meetingForm.disable();

            // Show success message and enable recommendations/attachments sections
            console.log(
              'Meeting created successfully with ID:',
              response.data.id
            );

            // Send stored recommendations to API
            this.sendStoredRecommendationsToAPI(response.data.id);

            // Navigate to meetings list after a short delay
            setTimeout(() => {
              this.router.navigate(['/meetings']);
            }, 100);
          } else {
            this.error.set(response.message || 'خطأ في إنشاء الاجتماع');
          }
          this.isSubmitting.set(false);
        },
        error: (err) => {
          this.error.set('خطأ في إنشاء الاجتماع');
          this.isSubmitting.set(false);
        },
      });
    } else {
      console.log('Form is not valid:', this.meetingForm.errors);
      console.log('Form controls:', this.meetingForm.controls);
    }
  }

  // Send stored recommendations to API
  private sendStoredRecommendationsToAPI(meetingId: number): void {
    const storedRecommendations = this.recommendations();
    if (storedRecommendations.length === 0) {
      return;
    }

    // Send each recommendation to API
    storedRecommendations.forEach((recommendationData, index) => {
      setTimeout(() => {
        const recommendation: MeetingRecommendationRequest = {
          text: recommendationData.text,
          assigneeUserId: recommendationData.assigneeUserId,
        };

        this.meetingsService
          .addRecommendation(meetingId, recommendation)
          .subscribe({
            next: (response) => {
              if (response.success) {
                console.log(
                  `Recommendation ${index + 1} sent successfully:`,
                  response.data
                );
              } else {
                console.error(
                  `Failed to send recommendation ${index + 1}:`,
                  response.message
                );
              }
            },
            error: (err) => {
              console.error(`Error sending recommendation ${index + 1}:`, err);
            },
          });
      }, index * 500); // Send with 500ms delay between each
    });
  }

  private buildAttendeesArray(attendees: any[]): MeetingAttendeeRequest[] {
    return attendees.map((attendee) => {
      const attendeeRequest: MeetingAttendeeRequest = {
        Kind: parseInt(attendee.kind, 10) as AttendeeKind,
        Role: parseInt(attendee.role, 10) as AttendeeRole,
        Notes: attendee.notes || '',
      };

      if (parseInt(attendee.kind, 10) === AttendeeKind.Internal) {
        attendeeRequest.UserId = attendee.userId;
        attendeeRequest.DisplayName = attendee.displayName;
        attendeeRequest.Department = attendee.department;
      } else {
        attendeeRequest.DisplayName = attendee.displayName;
        attendeeRequest.Organization = attendee.organization;
        attendeeRequest.JobTitle = attendee.jobTitle;
        attendeeRequest.Department = attendee.department;
        attendeeRequest.Email = attendee.email;
      }

      console.log('Built attendee:', attendeeRequest);
      return attendeeRequest;
    });
  }

  // Recommendations
  addRecommendation(recommendationText: string, assigneeUserId?: string): void {
    if (!recommendationText.trim() || this.isAddingRecommendation()) {
      return;
    }

    this.isAddingRecommendation.set(true);
    const recommendation: MeetingRecommendationRequest = {
      text: recommendationText.trim(),
      assigneeUserId: assigneeUserId || undefined,
    };

    // If meeting is already created, add to API
    if (this.createdMeetingId()) {
      this.meetingsService
        .addRecommendation(this.createdMeetingId()!, recommendation)
        .subscribe({
          next: (response) => {
            if (response.success) {
              this.recommendations.update((recs) => [
                ...recs,
                { text: recommendationText.trim(), assigneeUserId: assigneeUserId || undefined },
              ]);
              this.success.set(response.message);
              console.log('Recommendation added successfully:', response.data);
            } else {
              this.error.set(response.message || 'خطأ في إضافة التوصية');
              console.error('Failed to add recommendation:', response.message);
            }
            // Use setTimeout to ensure UI updates properly
            setTimeout(() => {
              this.isAddingRecommendation.set(false);
            }, 100);
          },
          error: (err) => {
            console.error('Error adding recommendation:', err);
            this.error.set('خطأ في إضافة التوصية');
            // Use setTimeout to ensure UI updates properly
            setTimeout(() => {
              this.isAddingRecommendation.set(false);
            }, 100);
          },
        });
    } else {
      // If meeting is not created yet, just add to local list
      this.recommendations.update((recs) => [
        ...recs,
        { text: recommendationText.trim(), assigneeUserId: assigneeUserId || undefined },
      ]);
      this.success.set('تم إضافة التوصية بنجاح');
      // Use setTimeout to ensure UI updates properly
      setTimeout(() => {
        this.isAddingRecommendation.set(false);
      }, 100);
    }

    // Force change detection
    this.cdr.detectChanges();
  }

  // Utility methods
  translate(key: string): string {
    return this.translationService.translate(key);
  }

  clearError(): void {
    this.error.set('');
  }

  clearSuccess(): void {
    this.success.set('');
  }

  getAttendeeKindLabel(kind: AttendeeKind): string {
    return kind === AttendeeKind.Internal
      ? this.translate('meetings.attendee.internal')
      : this.translate('meetings.attendee.external');
  }

  getAttendeeRoleLabel(role: AttendeeRole): string {
    switch (role) {
      case AttendeeRole.Required:
        return this.translate('meetings.attendee.required');
      case AttendeeRole.Optional:
        return this.translate('meetings.attendee.optional');
      case AttendeeRole.Speaker:
        return this.translate('meetings.attendee.speaker');
      default:
        return '';
    }
  }

  getMeetingTypeLabel(type: MeetingType): string {
    switch (type) {
      case MeetingType.Internal:
        return this.translate('meetings.type.internal');
      case MeetingType.External:
        return this.translate('meetings.type.external');
      case MeetingType.Other:
        return this.translate('meetings.type.other');
      default:
        return '';
    }
  }

  getLocationModeLabel(mode: MeetingLocationMode): string {
    switch (mode) {
      case MeetingLocationMode.InPerson:
        return this.translate('meetings.location.in_person');
      case MeetingLocationMode.Online:
        return this.translate('meetings.location.online');
      case MeetingLocationMode.Hybrid:
        return this.translate('meetings.location.hybrid');
      default:
        return '';
    }
  }
}
